<template>
  <div>
    <Banner/>
    <div class="container">
      <div class="row">
        <div class="col-md-11 col-md-offset-1">
          <steps :current="4">
              <step title="提交订单" content="订单的提交"></step>
              <step title="设置地址" content="设置收货地址"></step>
              <step title="确认订单" content="完成订单的确定"></step>
              <step title="完成支付" content="完成订单的支付"></step>
          </steps>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12">
          <h4 class="page-header">
            <i class="iconfont icon-iconyuyuechenggong iconyuyuechenggong"></i>ＳＵＣＣＥＳＳ
          </h4>
        </div>
      </div>
      <div class="row message">
        <div class="col-md-5 col-md-offset-4 successContainer">
          <p><img src="../../static/ok-2.png" alt="" class="successImg"></p>
          <h3>Congrutalation!</h3>
          <p>感谢您！为我们商城奉献了宝贵的财富！</p>
          <p>欢迎您下次继续光临本店，我们将继续为您服务！</p>
          <p>
            <span class="order">您的订单号：{{orderId}}</span>
            <span class="order">您订单总金额：{{orderTotalMoney | currency('￥')}}</span>
            <span class="order">订单创建时间：{{createOrderDate}}</span>
          </p>
        </div>
      </div>
      <div class="row">
        <div class="col-md-5 col-md-offset-4">
            <router-link class="shopping" to="/fruit">ＳＨＯＰＰＩＮＧ</router-link>
            <router-link class="goCart" to="/cart">ＧＯＣＡＲＴ</router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Banner from './Banner'
import {steps,step} from 'iview'
import axios from 'axios'
import {currency} from '../../util/currency'
export default{

  data(){

    return {
      orderTotalMoney:0,
      orderId:'',
      createOrderDate:''
    }
  },
  components:{
    Banner,
    steps,
    step
  },
  filters:{
    currency
  },
  mounted(){
    this.getSubmitOrderDetail();
  },
  methods:{

    shopping(){

      this.$router.push("")

    },

    getSubmitOrderDetail(){

      let orderId = this.$route.query.orderId;

      axios.post('/getSubmitOrder',{

        orderId:orderId

      }).then(res=>{

        let result = res.data.result

        this.orderTotalMoney = result.orderTotalMoney;

        this.orderId = result.orderId;

        this.createOrderDate = result.createOrderDate;

      })

    }

  }
}
</script>
<style>
.shopping{
  color: #999;
  text-align: center;
  display: inline-block;
  padding: 10px 20px;
  background: #ecb21596;
  margin: 0px 30px 0px 30px;
  cursor: pointer;
}
.goCart{
  color: #999;
  display: inline-block;
  padding: 10px 30px;
  /* background: #39e81680; */
  text-align: center;
  cursor: pointer;
}
.message{
  border-bottom: 1px solid #ef7f0ccf;
  margin: 10px 0px;
}
.order{
  display: block;
  margin-left: 100px;
  text-align: left;
  margin-right: 10px;
  color: #ef7f0ccf;
}
.iconyuyuechenggong{
  display: inline-block;
  font-size: 30px;
  color: #61cc1f;
}
.successImg{
  width: 120px;
  height: 180px;
}
.successContainer{
  text-align: center;
}
.successContainer h3,.successContainer p{
  margin: 15px 0px;
}
</style>



