<template>
  <div>
    <Banner />
    <div class="container">
      <div class="row">
        <div class="col-md-3">
          <div class="list-group">
            <a href="" class="list-group-item active text-center">个人资料</a>
            <a href="" class="list-group-item text-center">我的订单</a>
            <a href="" class="list-group-item text-center">我的钱包</a>
            <a href="" class="list-group-item text-center">待评价</a>
            <a href="" class="list-group-item text-center">售后</a>
            <a href="" class="list-group-item text-center">收藏</a>
            <a href="" class="list-group-item text-center">客户</a>
            <a href="" class="list-group-item text-center" >地址管理</a>
            <a href="" class="list-group-item text-center">我的优惠价</a>
          </div>
        </div>
        <div class="col-md-8 col-sm-8 Profile">
          <div class="pull-left Avatar">
            <img :src="'../../static/'+MyProfile.avatar" :alt="MyProfile.nickName" class="img-circle">
          </div>
          <div class="pull-left sampleProfile">
            <h4>{{MyProfile.nickName}}</h4>
            <p class="detailProfile"><span>{{MyProfile.sex}}</span><span>{{MyProfile.constellatory}}</span><span>{{MyProfile.place}}</span></p>
          </div>
          <div class="pull-right editProfile">
            <button type="">编辑资料</button>
            <div class="fans">
              <span>{{MyProfile.fans}}</span>
              <span>粉丝</span>
            </div>
            <div class="follow">
              <span>{{MyProfile.follow}}</span>
              <span>关注</span>
            </div>
          </div>
        </div>
        <div class="col-md-8 MyIntroduction">
          <p><span>我的爱好：</span><span v-for="item in MyProfile.myLove">{{item}}、</span></p>
          <p><span>我喜欢的水果类型：</span><span v-for="item in MyProfile.likeFruitType">{{item}}、</span></p>
          <p><span>我喜欢的水果：</span><span v-for="item in MyProfile.likeFruit">{{item}}、</span></p>
          <p><span>我的简介：</span></p>
          <p class="introduction">{{MyProfile.material}}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Banner from './Banner'
  import axios from 'axios'
  export default{
    data(){
      return {
        MyProfile:'',
      }
    },
    components:{
      Banner
    },
    mounted(){
      this.getUserIntroduction();
    },
    methods:{

      getUserIntroduction(){

        axios.post('/getUserDetail').then(res=>{

            if(res.data.status === 0){

              this.MyProfile = res.data.result;

            }else{

                alert('没有该用户')

            }

        }).catch(err=>{

          alert('其他错'+err)

        })

      }
    }
  }
</script>
<style>
.MyIntroduction p span{
  font: 500 16px/30px "楷体";
}
.introduction{
  text-indent: 20px;
  line-height: 30px;
}
.Profile{
  border-bottom: 1px solid #7ada1d;
  margin-bottom: 20px;
}
.Avatar{
  margin-right: 30px;
}
.sampleProfile{
  margin-top: 15px;

}
.sampleProfile h4{
  font: 18px "微软雅黑";
}
.detailProfile span{
  margin-left: 10px;
}
.editProfile button{
  margin: 20px 15px;
  display: block;
}
.fans,.follow{
  margin-left: 20px;
  display: inline-block;
}
.fans span,.follow span{
  display: block;
}
</style>



