<template>
  <div>
  <Banner/>
  <div class="container">
      <div class="media" v-for="item in fruitList" :key="item.id">
        <div class="media-left">
          <a href="javascript:;" class="smallImage">
            <img v-lazy="'../../static/mix-img/'+item.image[0].min" alt="" title="item.name">
          </a>
        </div>
        <div class="media-body detailBody">
          <h4 class="page-header">{{item.name}}</h4>
          <p class="detailedFruit">{{item.detail[0].detailed}}</p>
          <div class="praiseAndSalesNum">
              <div class="floatRight">
                <i class="iconfont icon-appreciate">{{item.praise}}</i>
                <i class="iconfont icon-hotfill">{{item.SalesNum}}</i>
              </div>
          </div>
         <div class="praiseAndSalesNum">
            <div class="floatRight">
              <button type="" class="btn btn-primary btn-lg">加入购物车</button>
            </div>
         </div>
        </div>
      </div>
    <div class="row">
      <div class="col-md-3 col-md-offset-2">
        <p><i class="iconfont icon-fanhui turnBack" @click="goFruits"><span>返回</span></i></p>
      </div>
    </div>
  </div>
  </div>
</template>
<script>
  import axios from 'axios'
  import Banner from './Banner'
  export default{
    data(){
      return {
        id:0,
        fruitList:[]
      }
    },
    components:{
      Banner
    },
    mounted(){
      this.getFruitDetail();
    },
    methods:{
      goFruits(){
        this.$router.push({path:'/fruit'})
      },
      getID(){
        this.id = this.$route.query.id;
        return this.id
      },
      getFruitDetail(){
        axios.get('/getFruitDetail',{
          params:{
            id:this.getID()
          }
        }).then(res=>{
            this.fruitList = res.data.result;
        })
      }
    }
  }
</script>
<style>
.detailBody{
  background: #f8f8f8;
}
  .detailedFruit{
    color: #555;
    font: 500 16px/30px "微软雅黑";
    text-indent:2em;
  }
  .floatRight{
    float: right;
  }
  .praiseAndSalesNum{
    margin-top: 10px;
   display: block;
  }
  .praiseAndSalesNum::after{
    content: '.';
    height: 0;
    display: block;
    visibility: hidden;
    clear: both;
  }
  .turnBack{
    display: inline-block;
    font-size: 40px;
    color: #777;
    cursor: pointer;
  }
  .turnBack:hover{
    transition: color .4s ease;
     color: #333;
  }
  .turnBack span{
    display: inline-block;
    font-size: 16px;
  }
</style>



