<template>
  <div>
    <Banner/>
    <div class="container">
      <div class="row">
        <div class="col-md-11 col-md-offset-1">
          <steps :current="1">
              <step title="提交订单" content="订单的提交"></step>
              <step title="设置地址" content="设置收货地址"></step>
              <step title="确认订单" content="完成订单的确定"></step>
              <step title="完成支付" content="完成订单的支付"></step>
          </steps>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12">
          <h4 class="page-header">
            <i class="iconfont icon-shouhuodizhi shouhuodizhi"></i>ＭＹＡＤＤＲＥＳＳ
          </h4>
        </div>
      </div>
      <div class="row">
        <div class="col-md-3 addressItem" v-for="item in addressList" :key="item.id">
          <div class="optionsAddress">
            <p>
              <i class="iconfont icon-xingming addressFont"></i>
              <span class="addressTitle">收货人：</span>
              <span>{{item.recipientName}}</span>
            </p>
            <p>
              <i class="iconfont icon-dianhua addressFont"></i>
              <span class="addressTitle">收货电话：</span>
              <span>{{item.recipientTel}}</span>
            </p>
            <p>
              <i class="iconfont icon-icon01 addressFont"></i>
              <span class="addressTitle">收货邮编：</span>
              <span>{{item.recipientPostcodes}}</span>
            </p>
            <p>
              <i class="iconfont icon-dizhi1 addressFont"></i>
              <span class="addressTitle">收货所在地：</span>
              <span>{{item.recipientAddress}}</span>
            </p>
            <p>
              <i class="iconfont icon-dizhi addressFont"></i>
              <span class="addressTitle">详细的地址：</span>
              <span>{{item.recipientDetailAddress}}</span>
            </p>
            <p>
              <i class="iconfont icon-shezhimoren addressFont"></i>
              <a href="javascript:;" @click="setDefalutAddress(item.addressId)">设置默认收货地址</a>
            </p>
          </div>
        </div>
        <div class="col-md-12">
          <div class="moreAddress">
              <i class="iconfont icon-xia xiafont"></i><span @click="moreAddress">moreAddress</span>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12">
          <h5 class="page-header">
            ＤＥＦＡＵＬＴＡＤＤＲＥＳＳ
          </h5>
        </div>
      </div>
      <div class="row">
        <div class="col-md-3" v-for="address in addressTemp" v-if="address.isDefaultAddress == 1">
          <div class="optionsAddress AddressDefalut">
            <p>
              <i class="iconfont icon-xingming addressFont"></i>
              <span class="addressTitle">收货人：</span>
              <span>{{address.recipientName}}</span>
            </p>
            <p>
              <i class="iconfont icon-dianhua addressFont"></i>
              <span class="addressTitle">收货电话：</span>
              <span>{{address.recipientTel}}</span>
            </p>
            <p>
              <i class="iconfont icon-icon01 addressFont"></i>
              <span class="addressTitle">收货邮编：</span>
              <span>{{address.recipientPostcodes}}</span>
            </p>
            <p>
              <i class="iconfont icon-dizhi1 addressFont"></i>
              <span class="addressTitle">收货所在地：</span>
              <span>{{address.recipientAddress}}</span>
            </p>
            <p>
              <i class="iconfont icon-dizhi addressFont"></i>
              <span class="addressTitle">详细的地址：</span>
              <span>{{address.recipientDetailAddress}}</span>
            </p>
          </div>
        </div>
      </div>
      <div class="row skipTo">
        <div class="col-md-2">
          <router-link class="previose" to="/cart">ＰＲＥＶＩＯＳＥ</router-link>
        </div>
        <div class="col-md-2 col-md-offset-8">
          <router-link class="next" to="/submit">ＮＥＸＴ</router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Banner from './Banner'
import {steps,step} from 'iview'
import axios from 'axios'
export default{
  data(){
    return {
      addressTemp:[],
      addressListShowMore:false
    }
  },
  mounted(){
    this.getUserAddress();
  },
  computed:{

    addressList(){

      let tempList=[];

      if(this.addressListShowMore){

            tempList = this.addressTemp;

          }else{

            tempList = this.addressTemp.slice(0,4);

          }
        return tempList;
      }
  },
  methods:{
    moreAddress(){
        this.addressListShowMore = true;
    },
    getUserAddress(){

      axios.post('/getUserAddress').then(res=>{

       if(res.data.status === 0){

          this.addressTemp = res.data.result;

       }else{

          alert('查询失败，您是否未登录或者该用户不存在？')

       }

      }).catch(err=>{

        alert('其他方面的错误')

      })
    },
    setDefalutAddress(addrId){

      axios.post('/setDefalutAddress',{

        params:{

          addressId:addrId

        }
      }).then(res=>{

        if(res.data.status == 0){

            //alert('设置成功');

            this.getUserAddress();

        }else{

          alert('设置失败');
        }

      }).catch(err=>{

        alert(err);

      })
    }
  },
  components:{
    Banner,
    steps,
    step
  }

}


</script>
<style>
.addressItem{
  margin-bottom: 15px;
}
.shouhuodizhi{
  display: inline-block;
  font-size: 30px;
  margin-right: 10px;
  color: #1bd3dcd1;
}
.optionsAddress{
  border: 1px solid #acecaa;
  border-radius: 20px;
  padding: 5px;
  color: #aaa;
}
.AddressDefalut{
  border: 1px solid #6be267;
  color: #777;
}
.optionsAddress p{
  line-height: 30px;
  margin-left: 10px;
}
.addressTitle{
  font: 500 14px "微软雅黑";
}
.addressFont{
  display: inline-block;
  font-size: 20px;
  margin-right: 15px;
}
.optionsAddress:hover{
  border: 1px solid #6be267;
  box-shadow: 0px 0px 10px #ddd;
  color: #777;
}
.addAddress{
  border: 1px solid #acecaa;
  position: relative;
  height: 100px;
  width: 100px;
  top: 50px;
  left: 100px;
}
.addAddressMark{
  font-size: 50px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -20px;
  margin-top: -30px;
}
.moreAddress{
  font: 500 14px/30px "微软雅黑";
  text-align: center;
  margin-top: 10px;
  height: 30px;
  width: 100%;
  border: 1px solid #ddd;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
  color: #aaa;
  cursor: pointer;
}
.moreAddress:hover{
  border: 1px solid #999;
  color: #777;
}
.previose{
  display: inline-block;
  border:1px solid  #f9c8c842;
  font: 16px/20px "微软雅黑";
  padding: 7px;
  width: 150px;
  background: #f3aaaa63;
  cursor: pointer;
}
.previose:hover{
  background:#f3aaaa96;
}
.skipTo{
  margin: 20px 0px 20px 0px;
}
.next{
  display: inline-block;
  border:1px solid  #5ef16863;
  font: 16px/16px "微软雅黑";
  padding: 7px 40px;
  background: #5ef16863;
  cursor: pointer;
  width: 150px;
}
.next:hover{
   background: #66f3708f;
}
</style>



