<template>
  <div :class="{'fruit-modal':changeModal}"  v-if="changeModal" @click="fruitClose">
     <div :class="{'fruit-modal-container':changeModal}" @click.stop>
        <div class="fruit-header-close">
          <span @click="fruitClose">×</span>
        </div>
        <div class="fruit-header">
          <slot name="add"></slot>
        </div>
        <div class="fruit-content">
            <slot name="addBtn"></slot>
        </div>
     </div>
  </div>
</template>
<script>
  export default{
    data(){
      return {
      }
    },
    props:['changeModal'],

    methods:{

      fruitClose(){
        this.$emit('closeModal')
      }
    }
  }
</script>
<style>
/*   .fruit-modal{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    right: 0;
    top: 0;
    background: rgba(39,38,38,.8);
    z-index: 10;
  } */
  .fruit-modal-container{
    width: 400px;
    height: 200px;
    background: #ecedef;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -200px;
    margin-top: -150px;
    z-index: 100;
    border-radius: 20px;
  }
  .fruit-header-close {
    line-height: 26px;
    text-align: center;
    cursor: pointer;
  }
  .fruit-header-close::after{
    content: ".";
    clear: both;
    display: block;
    font-size: 0;
    height: 0;
  }
  .fruit-header-close span{
    float: right;
    font-size: 26px;
  }
  .fruit-header{
    margin-top: 30px;
    text-align: center;
  }
  .fruit-header-iconfont {
    font-size: 40px;
    color: #e6bb1b;
    margin-right: 10px;
    display: inline-block;
    vertical-align: middle;
  }
  .fruit-header span{
    font-size: 20px;
    font-family: "微软雅黑";
    color: #333;
    display: inline-block;
    vertical-align: middle;
  }
  .fruit-content{
    margin-top: 25px;
    text-align: center;
  }
  .fruit-content button{
    border: 0 none;
    display: inline-block;
  }

</style>



