import Vue from 'vue'
import vuex from 'vuex'
import * as state from './states'
import * as types from './type'
import * as actions from './actions'

Vue.use(vuex);

export default new vuex.Store({

    state,
    mutations:{
      [types.UPDATE_GOODSNUMBER](state,option){
        switch (option.operation) {
          case 2:
             state.goodsCounter = option.data;
            break;
          case -1:
             state.goodsCounter -= 1;
            break;
          case 1:
             state.goodsCounter += 1;
             break;
          case 0:
             state.goodsCounter -= option.num;
             break;
          case -3:
             state.goodsCounter = 0;
             break;
        }
      }
    },
    actions

})



