<template>
  <div>
   <Banner/>
    <div class="row">
      <div class="col-md-12 col-sm-12 col-lg-12"  id="fruits">
        <div class="row">
          <div class="col-md-5  col-xs-12 col-md-offset-1 col-xs-offset-1">
            <div class="FruitsType" @click="classification($event)">
              <span class="classify"><i class="iconfont icon-icon fruittype"></i>分类：</span>
              <span><input type="radio" value='1' name="fruit"/>温性水果</span>
              <span><input type="radio" value="2" name="fruit"/>热性水果</span>
              <span><input type="radio" value='3' name="fruit"/>寒性水果</span>
            </div>
          </div>
          <div class="col-md-3 col-md-offset-3 text-justify">
            <div class="orderBy-fruits">
              <span><i class="iconfont icon-paixu paixun"></i>orderBy:</span>
              <a href="javascript:;" @click="changeOrderPlay($event,1)" v-if="orderPlay">up</a>
              <a href="javascript:;" @click="changeOrderPlay($event,0)" v-if="!orderPlay">down</a>
              <span >↑</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-3 col-sm-3 col-lg-3 col-xs-12 fruit-price">
        <ul class="fruit-price" >
          <li v-for="(item,index) in fenlei" :key="item.id">
            <a href="javascript:;"  @click="Assortment(index,$event)" class="">{{item}}</a>
          </li>
        </ul>
      </div>
      <div class="col-md-9 col-sm-9 col-lg-9 col-xs-12">
        <div class="row" id="fruits-item">
          <div class="col-sm-4 col-md-4 col-lg-4" v-for="fruit in fruits" :key="fruit.id">
            <div class="thumbnail">
              <a href="javascript:;" @click="FruitDetail(fruit.id)"><img v-lazy="'../../static/mix-img/'+fruit.image[0].min" alt="" :title="fruit.name"></a>
              <div class="caption fruits-detail">
                <p><span class="fruitName">名称：<i class="fruitNameValue">{{fruit.name}}</i></span></p>
                <p><span class="fruitPrice">价格：<i class="fruitPriceValue">{{fruit.price}}￥</i></span></p>
                <p><span class="fruitPrice">产地：<i class="fruitPlace">{{fruit.originPlace}}</i></span></p>
                <p class="fruit-item-detail"><span class="condactFruit">简介：</span><a href="javascript:;" class="fruitDetail" @click="FruitDetail(fruit.id)">{{fruit.detail[0].detailed}}</a>
                </p>
                <p>
                  <a href="javascript:;" class="praise"><i class="iconfont icon-appreciate"></i>{{fruit.praise}}</a>
                  <a href="javascript:;" class="SalesNum"><i class="iconfont icon-hotfill"></i>{{fruit.SalesNum}}</a>
                </p>
                <p><i class="iconfont icon-cart_light iconfont-btn" @click="addCart(fruit.id)"></i></p>
              </div>
            </div>
          </div>
        </div>
        <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10" class="fruit-loading">
            <img v-lazy="'../../static/loading-svg/loading-spinning-bubbles.svg'"  v-show="fruitLoading" alt="">
        </div>
      </div>
    </div>
    <Modal :changeModal="fruitModal" v-on:closeModal="close">
      <p slot="add">
        <i class="iconfont icon-tishichenggong fruit-header-iconfont" v-if="addSuccessFlag">
        </i>
        <i class="iconfont icon-anzhuangshibai fruit-header-iconfont" v-if="addErrorFlag"></i>
        <i class="iconfont icon-jiazaishibai fruit-header-iconfont" v-if="NLoginFlag"></i>
        <span v-if="addSuccessFlag">{{addSuccessSkip}}</span>
        <span v-if="NLoginFlag">{{NoLoginSkip}}</span>
        <span v-if="addErrorFlag">{{addError}}</span>
      </p>
      <span slot="addBtn" class="buttonGroup">
        <button type="" class="goCart" @click="goCart">前往购物车</button>
        <!--用vuex来写能不能前往购物车-->
        <button type="" class="goClose" @click="close">继续购物</button>
      </span>
    </Modal>
  </div>
</template>
<script>
  import axios from 'axios'
  import Modal from './modal'
  import Detail from './Detail'
  import Banner from './Banner'
  import {mapMutations} from 'vuex'
  export default {
    name:'Model',
    data(){

      return {

        orderPlay:false,
        fruits:[],
        fruitModal:false,
        busy:true,
        page:1,
        FruitSort:1,
        fenlei:[],
        fenleiFlag:0,
        fruitLoading:false,
        fruitType:'',
        addSuccessSkip:'',
        addSuccessFlag:false,
        addErrorFlag:false,
        addError:'',
        NLoginFlag:false,
        NoLoginSkip:'',
        clickFlag:false,
      }
    },
    components:{
      Modal,
      Detail,
      Banner
    },
    mounted(){

      this.getFruits();

    },

    methods:{

      ...mapMutations(['UPDATE_GOODSNUMBER']),

      close(){

        this.fruitModal = false;

      },
      goCart(){

        this.$router.push({path:'/cart'});

      },

      classification(event){

        var flag = event.target.value;

        this.fruitType = flag;

        this.getFruits();

      },

      Assortment(index,event){
        let childList = event.target.parentNode.parentNode.children;
        [...childList].forEach(item1=>{
          let arr = item1.children;
          [...arr].forEach(ele=>{
            let eleClass = ele.className.trim().split(" ");
            let eleClassList;
            if(eleClass.indexOf('clickStyle') >-1){
                eleClass.splice(eleClass.indexOf('clickStyle'),1);
                ele.className = eleClass.toString();
            }else{
               ele.className = eleClass.toString();
            }
          })
        });
        event.target.className+=' clickStyle';
        switch(index){

          case 0: this.fenleiFlag =0;
                  this.page = 1;
                  this.getFruits();
                  break;
          case 1: this.fenleiFlag =1;
                  this.page = 1
                  this.getFruits();
                  break;
          case 2: this.fenleiFlag =2;
                  this.page = 1
                  this.getFruits();
                  break;
          case 3: this.fenleiFlag =3;
                  this.page = 1
                  this.getFruits();
                  break;
          case 4: this.fenleiFlag =4;
                  this.page = 1
                  this.getFruits();
                  break;
        }
      },

      getFruits(flag){

        axios.get('/fruits',{

        params:{

          fenlei:this.fenleiFlag,

          page:this.page,

          sort:this.FruitSort,

          fruitType:this.fruitType

        }

        }).then(res=>{

            if(flag){

              this.fruits = this.fruits.concat(res.data.result);

              if(res.data.count == 0){

                this.busy = true;

                this.fruitLoading = false;

              }else{

                this.fruitLoading = false;

                this.busy = false;

              }

            }else{

              this.fruits = res.data.result;

              this.fenlei = res.data.assortment;

              this.busy = false;
            }
        })
      },
      changeOrderPlay(event,flag){
      if(flag ==1){
        event.target.parentNode.lastChild.className="orderPlay";

      }else{
         event.target.parentNode.lastChild.className="orderPlay1";
      }

      this.orderPlay=!this.orderPlay;

      this.FruitSort =- (this.FruitSort);

      this.page = 1;

      this.getFruits();

    },
    addCart(id){

      axios.post('/addCart',{

        params:{

          id:id

        }

      }).then(res=>{

        if(res.data.status == 0){

            this.fruitModalShow=false;

            this.fruitModal = true;

            this.addSuccessFlag = true;

            this.NLoginFlag = false;

            this.addErrorFlag = false;

            this.addSuccessSkip = res.data.msg;

            this.UPDATE_GOODSNUMBER({operation:1});

        }else if(res.data.status == 1){


          this.fruitModalShow=false;

          this.fruitModal = true;

          this.addErrorFlag = true;

          this.NLoginFlag = false;

          this.addSuccessFlag = false;

          this.addError = res.data.msg;

        }else if(res.data.status == 10010){


          this.fruitModalShow=false;

          this.fruitModal = true;

          this.NLoginFlag = true;

          this.addSuccessFlag = false;

          this.addErrorFlag = false;

          this.NoLoginSkip = res.data.msg;

        }

      })

    },
    loadMore(){

      this.fruitLoading = true;

      this.busy=true;

      setTimeout(() => {

        this.page++

        this.getFruits(true);

      }, 1000);
    },
    FruitDetail(id){

      this.$router.push({path:'/detail',query:{id:id},component:Detail})

    }
  }
}
</script>
<style>
.fruittype{
  display: inline-block;
  font-size: 20px;
  color: #e80dd7;
}
.paixun{
  display: inline-block;
  font-size: 20px;
  color: #000;
}
 .goCart{
    color: #fff;
    background: #418cce;
    width: 150px;
    height: 40px;
  }
  .goClose{
    color: #fff;
    background: #418cce;
    width: 150px;
    height: 40px;
  }
.classify{
  font: 16px "微软雅黑";
  color: #5a5555;
}
 i{
    font-style: normal;
  }
.FruitsType{
  margin: 15px;
}
.FruitsType span{
  margin-right: 10px;
  font: 14px;
  display: inline-block;
  vertical-align: middle;
}
.FruitsType span input{
}
.condactFruit{
  font: 600 14px "微软雅黑";
}
.fruit-loading{
  width: 40px;
  height: 40px;
  margin: 0 auto;
}
.SalesNum{
  color: red;
}
.praise{
  color: #333;
}
.changeModal{
  transition: display 1.2s ease;
  display: none;
}
#fruits{
  background-color: #ecedef;
  margin-bottom: 20px;
  height: 50px;
}
@media screen and (max-width:970px){
  .orderBy-fruits{
    display:none;
  }
}

.orderBy-fruits{
    margin-top: 15px;
    margin-right: 30px;
    font-size: 16px;
}
.orderBy-fruits a,.fruit-price  a{
  text-decoration: none;
}
.fruit-price li a {
  display: block;
  font: 16px "微软雅黑";
  color: #f5990f;
}
.fruit-price li{
  padding: 10px;
  list-style: none;
}
.fruit-price li a:hover{
  transition: all .7s ease ;
  padding: 0 20px;
  border-left: 3px solid #f5990f;
}
.clickStyle{
  padding: 0 20px;
  border-left: 3px solid #f5990f;
}
.fruits-detail{
  text-align: center;
}
.thumbnail:hover{
  border: 1px solid #efe0518a;
  transition: transform .5s ease;
  transform: translate3d(0,-4px,0);
  box-shadow: 0px 15px 30px rgba(245,153,15,0.2);
}
.orderBy-fruits span{
  display: inline-block;
  margin-left: 5px;
  font-size: 16px;
  /* margin-right: 10px; */
}
.orderPlay{
  transition: all .8s ease;
  transform: rotate(0deg);
}
.orderPlay1{
  transition: all .8s ease;
  transform: rotate(180deg);
}
.fruit-item-detail{
  text-overflow:ellipsis;
  white-space:nowrap;
  overflow: hidden;
}
.fruitName {
  font: 600 16px "微软雅黑";
}
.fruitNameValue{
   font:14px "微软雅黑";
   color: #e4990f;
}
.fruitPrice{
   font:14px "微软雅黑";
  color: red;
}
.fruitPriceValue{
   font: 600 16px "微软雅黑";
}
.iconfont-btn{
  display:block;
  width: 100%;
  height: 40px;
  background: #0aa0e4;
  font-size: 20px;
  line-height: 40px;
}
.iconfont-btn:hover{
  border:1px solid #0a7db1;
  cursor: pointer;
  background: #0a7db1;
}
</style>



