import * as types from './type'
import api from '../api/'

export  function editStateGoodsNumber({commit}){

    api.getGoodsCounter().then(res=>{

     if(res.data.status ===0){

         commit(types.UPDATE_GOODSNUMBER,{

          operation:2,

          data:res.data.result

         });

      }
  })

}



