<template>
  <div class="row">
    <nav class="navbar navbar-default">
      <div class="container-fluid">
        <div class="navbar-header">
           <router-link class="navbar-brand" to="/fruit"><i class="iconfont icon-shuiguo shuiguo"></i>Fruits</router-link>
        </div>
        <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
          <ul class="nav navbar-nav navbar-right">
            <li :class="{'showLogin':loginSuc}">
              <a href="javascript:;" @click="loginshow"><i class="iconfont icon-denglu denglu"></i>Login</a>
            </li>
            <li :class="{'showLogin':!loginSuc}">
              <router-link class="nick" to="/personal"><i class="iconfont icon-nicheng nicheng"></i>{{nickName}}</router-link>
            </li>
            <li :class="{'showLogin':!loginSuc}">
              <a href="javascript:;" class="loginout" @click="loginOut"><i class="iconfont icon-tuichudenglu tuichudenglu"></i>OUT</a>
            </li>
            <li>
              <a href="javascript:;">
                 <span><i class="iconfont icon-cart_light gouwuche1"></i></span>
                 <span @click="goCart">购物车<span class="badge" v-if="goodsCounter !== 0">{{goodsCounter}}</span></span>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
    <div class="loginContainer" v-if="showLoginOver">
      <div class="bannerCloseContainer">
          <span class="bannerClose" @click="close">×</span>
      </div>
      <p>
        <i class="iconfont icon-diannaodenglu loginAvatar"></i>
        <span class="loginOn">Login</span>
      </p>
      <div class="errorContainer">
        <p  class="showError" v-if="errorSkip">
          <i class="iconfont icon-cuowujinzhi wrongPrompt"></i>
          <span class="wrongPrompt">密码或者账号错误</span>
        </p>
        <p class="showErrorNull" v-if="errorNull">
          <i class="iconfont icon-cuowujinzhi wrongPrompt"></i>
          <span class="wrongPrompt">帐号或者密码不能为空</span>
        </p>
      </div>
      <p>
        <i class="iconfont icon-denglu FruitLoginFont"></i>
        <input type="text" name="" class="FruitLogin" v-model="username" placeholder="username" />
      </p>
      <p>
        <i class="iconfont icon-mima FruitResetFont"></i>
        <input type="password" name="" class="FruitPassword" v-model="password" placeholder="password">
      </p>
      <p class="inputBtn">
        <button type="" class="loginBtn" @click="login">登录</button>
        <button type="" class="resetBtn" @click="reset">重置</button>
      </p>
    </div>
    <div class="fruit-modal" v-show="showLoginOver" @click="close">
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  import {mapState,mapActions} from 'vuex'
  export default {
    data(){
      return {
        errorSkip:false,
        errorNull:false,
        showLoginOver:false,
        username:'',
        password:'',
        loginSuc:false,
        nickName:'',
        readyLogin:false,
      }
    },
    mounted(){

      this.checkLogin();

    },

    created(){
      this.editStateGoodsNumber()
    },

    computed:{
      ...mapState({
        goodsCounter:state=>state.goodsCounter
      })
    },
    watch:{

      username:{

        handler(val,old){

          if(val.lenngth !==0 ){

            this.skipShow();

          }
        }
      },
      password:{

        handler(val,old){

          if(val.lenngth !== 0){

            this.skipShow();

          }
        }
      }
    },
    methods:{

      ...mapActions(['editStateGoodsNumber']),

    Personal(){


      this.$router.push({path:'/personal'});

    },

    goFruit(){

      this.$router.push({path:'/fruit'})

    },

      loginshow(){

        this.showLoginOver = true;

      },

       goCart(){

        if( this.readyLogin){

        this.$router.push({path:'/cart'});

        }else{

            alert("你还未登录,请先登录！");

        }


      },

      close(){

          this.username = '';

          this.password = '';

          this.showLoginOver = false;

      },

      skipShow(){

        this.errorNull = false;

        this.errorSkip = false;

      },

      reset(){

        this.username = '';

        this.password = '';

      },
      login(){

        if(!this.username || !this.password){

          this.errorNull = true;

        }else{

        axios.post('/getUser',{

          params:{

            username:this.username,

            password:this.password

          }

        }).then(res=>{

          if(res.data.status == 0){

              alert(res.data.msg)

              this.loginSuc = true;

              this.readyLogin = true;

              this.showLoginOver = false;

              this.nickName = res.data.result.nickName;

              this.userID = res.data.result.userId;

          }else{

              this.errorSkip = true;
            }
          })
        }
      },
      loginOut(){

        var result = confirm("确定退出？");

        if(result){

          axios.post('/loginout').then(res=>{

            if(res.data.status ==0 ){

              this.loginSuc = false;

              this.readyLogin = false;

              this.username = '';

              this.password = '';
            }
          })
        }
      },
      checkLogin(){

        axios.post('/checkLogin').then(res=>{

           if(res.data.status == 0){

              this.loginSuc = true;

              this.readyLogin = true;

              this.nickName = res.data.result.nickName;
          }
        })
      }
    }
  }
</script>
<style>
.gouwuche1{
  display: inline-block;
  font-size: 20px;
  color: #e81010;
  font-weight: bold;
}
.denglu{
  display: inline-block;
  font-size: 19px;
  color: #11a0cc;
}
.tuichudenglu{
  display: inline-block;
  font-size: 17px;
  color: #ef2323d1;
}
.nicheng{
  margin-right: 2px;
  display: inline-block;
  font-size: 17px;
  color: #0fde29;
}
.shuiguo{
  display: inline-block;
  font-size: 38px;
  color: #49bf19;
}
.badge{
  background: #f93a3ae0;
  position: relative;
  left: -3px;
  top: -6px;
}
.inputBtn{
  margin-top: 20px;
}
.bannerClose{
  font-size: 24px;
  float: right;
  margin-right: 5px;
  cursor: pointer;
}
.bannerCloseContainer::after{
  content: '.';
  display: block;
  height: 0;
  visibility: hidden;
  clear: both;
}
.fruit-modal{
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  top: 0;
  background: rgba(39,38,38,.8);
  z-index: 10;
}
.loginContainer{
  width: 350px;
  height: 250px;
  position: absolute;
  left: 50%;
  top: 50%;
  background: #ddd;
  z-index: 200;
  margin-left: -175px;
  margin-top: -125px;
  border-radius: 20px;
  text-align: center;
}
.errorContainer{
  height: 20px;
}
.showError{
  margin-left: -50px;
  display: inline-block;
}
.showErrorNull{
  margin-left: -20px;
  display: inline-block;
}
.wrongPrompt{
  color: red;
  font-size: 14px;
}
.loginOn{
  display: inline-block;
  vertical-align: middle;
}
.loginAvatar{
  display: inline-block;
  font-size: 30px;
  vertical-align: middle;
}
.loginBtn{
  width: 140px;
  height: 30px;
  background: #2d72ef;
  border: 1px solid #2d72ef;
  color: #fff;
  border-radius: 10px;
  outline:none
}
.resetBtn{
  width: 140px;
  height: 30px;
  background: #fd4545;
  border: 1px solid #fd4545;
  color: #fff;
  border-radius: 10px;
}
.FruitLogin{
  outline: none;
  border: 1px solid #3b78b7;
  height: 30px;
  width: 200px;
  padding: 0 20px;
  border-radius: 10px
}
.FruitPassword{
  margin:10px;
  outline: none;
  border: 1px solid #3b78b7;
  height: 30px;
  width: 200px;
  padding: 0 20px;
  border-radius: 10px
}
.FruitLoginFont{
  position: relative;
  left: 23px;
}
.FruitResetFont{
  position: relative;
  left: 34px;
}
.nick{
  display: inline-block;
  padding: 15px 4px !important;
}
.loginout{
  margin-left: 5px;
   padding: 15px 0px !important;
}
.showLogin{
  display: none !important;
}
</style>



