<template>
  <div>
    <Banner/>
    <div class="row">
        <div class="col-md-11 col-md-offset-1">
          <steps :current="0">
              <step title="提交订单" content="订单的提交"></step>
              <step title="设置地址" content="设置收货地址"></step>
              <step title="确认订单" content="完成订单的确定"></step>
              <step title="完成支付" content="完成订单的支付"></step>
          </steps>
        </div>
    </div>
    <div class="row">
      <div class="col-md-11">
        <h4 class="page-header"><i class="iconfont icon-gouwuche gouwuche"></i>ＭＹＣＡＲＴ</h4>
      </div>
    </div>
    <div class="row">
      <div class="col-md-12 cartTitle col-xs-12">
        <div class="col-md-4">
            <p>ＦＲＵＩＴ</p>
        </div>
        <div class="col-md-2">
          <p>ＰＲＩＣＥ</p>
        </div>
        <div class="col-md-2">
          <p>ＭＵＬＴＩＬＳＥＬＥＣＴＥＤ</p>
        </div>
        <div class="col-md-2">
          <p>ＴＯＴＡＬＰＲＩＣＥ</p>
        </div>
        <div class="col-md-2">
          <p>ＩＳＤＥＬＥＴＥ</p>
        </div>
      </div>
      <div class="col-md-12 NoDataContainer" v-if="NoData">
        <div class="col-md-5 col-md-offset-4">
          <p>
            <i class="iconfont icon-meiyoudingdan-01 NoDataFont"></i>
            <span  class="NoDataSkip">还没有数据，请前往商城挑选你所想要的！</span>
          </p>
        </div>
      </div>
      <div class="col-md-12" v-for="item in cartList" :key='item.id'>
        <div class="col-md-4">
          <p class="cartGoodList">
            <input type="checkbox" name="" :checked="item.checked == 1" @click="edit('checked',item)">
            <img :src="'../../static/mix-img/'+item.image[0].min" :alt="item.name" class="image">
            <span class="cartGoodName">{{item.name}}</span>
          </p>
        </div>
        <div class="col-md-2">
          <p class="cartGoodList">
            <span class="cartFruit">{{item.price | currency}}</span>
          </p>
        </div>
        <div class="col-md-2">
          <p class="cartOption">
            <span class="operation decrease" @click="edit('reduce',item)">-</span>
            <span class="goodNumber">{{item.goodsNumber}}</span>
            <span class="operation increase" @click="edit('add',item)">+</span>
          </p>
        </div>
        <div class="col-md-2">
          <p class="cartGoodList"><span class="cartFruit">{{(item.goodsNumber*item.price) | currency}}</span></p>
        </div>
        <div class="col-md-2">
          <p class="cartGoodList">
            <span class="cartFruit" @click="deleteGoods(item.id,item.goodsNumber)"><i class="iconfont icon-msnui-trash deleteFont"></i></span>
          </p>
        </div>
      </div>
    </div>
    <div class="row utilskipContainer">
      <div class="col-md-4 col-xs-4">
        <p>
          <a href="avascript:;" class="selectedAll" @click="selectAll">
            <a href="javascript:;" class="selectBtn" v-if="showAll"></a>
          </a>
          <span class="selectAllTitle">ＳＥＬＥＣＴＡＬＬ</span>
          <a href="javascript:;" class="deleteAll" @click="deleteAll">DELETEALL</a>
        </p>
      </div>
      <div class="col-md-3 col-xs-3">
        <p>
          <a href="javascript:;">
            <i class="iconfont icon-ERP_huokuanzongjine moneyFont" ></i>
            <span class="totalPrice">TOTALMONEY：{{totalPrice | currency}}</span>
          </a>
        </p>
      </div>
      <div class="col-md-4 col-lg-offset-1 col-xs-4">
         <p class="pull-right">
            <span class="continueShopping" @click="goShopping"><i class="iconfont icon-gouwu1 nextFont"></i></span>
            <span class="Next" v-if="isNextFlag" @click="submit"><i class="iconfont icon-icon- nextFont"></i></span>
         </p>
      </div>
    </div>
    <Modal :changeModal="fruitModal" v-on:closeModal="close">
      <p slot="add">
        <i class="iconfont icon-exclaim deleteTitleFont"></i>
        <span class="deleteTitle">确定删除？</span>
      </p>
      <span slot="addBtn">
          <span class="yesDelete" @click="yesDelete">确定</span>
          <span class="cancelDelete" @click="cancelDelete">取消</span>
      </span>
    </Modal>
    <div v-if="successModalFlag" class="successModal">
      <p slot="add">
        <i class="iconfont icon-tishichenggong deleteTitleFont"></i>
        <span class="deleteTitle">删除成功</span>
      </p>
    </div>
  </div>
</template>
<script>
  import Banner from './Banner'
  import axios from 'axios'
  import Modal from './modal.vue'
  import {currency} from '../../util/currency'
  import {steps,step} from 'iview'
  import {mapMutations} from 'vuex'
  export default{
    data(){
      return{
        showBtn:false,
        cartList:[],
        NoData:false,
        IsDelete:false,
        fruitModal:false,
        goodsID:'',
        successModalFlag:false,
        successModal:false,
        delGoodsNumber:0
      }
    },
    components:{
      Banner,
      Modal,
      steps,
      step
    },
    watch:{
      successModal:{

        handler(val,old){

          this.successModalFlag = val;

        }
      }
    },
    mounted(){
      this.getCartList();
    },
    filters:{
      currency
    },
    computed:{
      isNextFlag(){
        let i=0;
         this.cartList.forEach((item)=>{
          if(item.checked ==1){
            ++i;
          }
        })
         if(i>0){
          return true;
         }else{
          return false;
         }
      },
      //时时进行计算
      showAll(){
        if(this.cartList.length===0){
          return false;
        }
        return this.checkedCount == this.cartList.length;
      },
      checkedCount(){
        let i=0;
        this.cartList.forEach((item)=>{
          if(item.checked ==1){
            ++i;
          }
        })
        return i;
      },
       totalPrice(){

        let totalMoney=0;

        this.cartList.forEach((item)=>{

          if(item.checked === 1){

            totalMoney +=item.price*item.goodsNumber
          }

        })
        return totalMoney;
      }
    },
    methods:{

      ...mapMutations(['UPDATE_GOODSNUMBER']),

      submit(){
        this.$router.push({path:'/setAddr'});
      },

      yesDelete(){

        let id = this.goodsID;

        let number = this.delGoodsNumber;

        this.IsDelete = true;

        if(this.IsDelete == true){

          axios.post('/deleteGoods',{

          params:{

            goodsId:id

          }

        }).then(res=>{

          if(res.data.status == 0){

            this.getCartList();

            this.fruitModal = false;

            this.successModal = true;

            this.successModalFlag = true;

            this.UPDATE_GOODSNUMBER({operation:0,num:number});

            setTimeout(()=>{

              this.successModal = false;

            },500)

            }else{

              alert('删除失败')

            }

          })
        }
      },

      cancelDelete(){

        this.fruitModal = false;

      },

      close(){

          this.fruitModal = false;
      },

      deleteAll(){

        var flag = confirm("亲都是您所喜欢的，是否删除全部？");

        if(flag){

          axios.post('/deleteAll').then(res=>{

              if(res.data.status === 0){

                this.getCartList();

                this.UPDATE_GOODSNUMBER({operation:-3});

              }else{

                alert('删除失败');

              }
          })
        }
      },
      deleteGoods(id,num){

        this.fruitModal = true;

        this.goodsID = id;

        this.delGoodsNumber = num;
      },
      edit(flag,goods){

        if(flag === 'add'){

          goods.goodsNumber++;

          this.UPDATE_GOODSNUMBER({operation:1})

        }else if(flag === 'checked'){

          goods.checked = goods.checked === 1?0:1;

        }else{

          if(goods.goodsNumber>1){

            goods.goodsNumber--

             this.UPDATE_GOODSNUMBER({operation:-1})

          }
        }

        axios.post('/edit',{

          params:{

            goodsId:goods.id,

            goodsNumber:goods.goodsNumber,

            checked:goods.checked

          }
        }).then(res=>{

          if(res.data.status === 0){

            this.getCartList();

          }else{

            alert('修改失败！')

          }

        }).catch(err=>{

          console.log("修改时未知错误!"+err)

        })
      },
      goShopping(){
        this.$router.push({path:'/fruit'})
      },
      selected(){
        this.showBtn = !this.showBtn;
      },
      selectAll(){
        let flag = !this.showAll;
        let Selectflag =flag?1:0;
        axios.post('/editCheckedAll',{
          params:{
            checkedAll: Selectflag
          }
        }).then(res=>{

          if(res.data.status == 0){

            this.getCartList();

          }else{

            alert('全选失败!');

          }

        })
      },

      getCartList(){

        axios.post('/cartList').then(res=>{

          if(res.data.status === 0){

            if(res.data.result.length === 0){

              this.NoData = true;

               this.cartList=[];

            }else{

              this.NoData = false;

              this.cartList = res.data.result;
            }

          }else{

            alert('请求失败，请检查网络或者请求地址是否正确？')

          }

        })
      }
    }
  }
</script>
<style>
.successModal{
  text-align: center;
  position: fixed;
  left: 50%;
  top: 50%;
  background: #dddd;
  width: 200px;
  height: 80px;
  margin-left: -100px;
  margin-top: -40px;
  line-height: 80px;
  border-radius: 40px;
  opacity: 0.8;
}
.yesDelete{
  display: inline-block;
  width: 140px;
  height: 30px;
  font: 16px/30px "微软雅黑";
  background: #e6a210;
  border-radius: 15px;
  vertical-align: middle;
  color: #e21a1a;
  cursor: pointer;
}
.cancelDelete{
  display: inline-block;
  width: 140px;
  height: 30px;
  font: 16px/30px "微软雅黑";
  background: #3886e2;
  border-radius: 15px;
  vertical-align: middle;
  color:#e0caca;
  cursor: pointer;
}
.deleteTitle{
  display: inline-block;
  font: 600 20px "微软雅黑";
  vertical-align: middle;
}
.deleteTitleFont{
  display: inline-block;
  font-size: 30px;
  color: red;
  vertical-align: middle;
}
.nextFont{
  color: #546964;
  display: inline-block;
  font-size: 25px;
  vertical-align: middle;
}
.moneyFont{
  color: #2fdc24;
  display: inline-block;
  font-size: 30px;
  vertical-align: middle;
}
.totalPrice{
  color: #ddd;
  display: inline-block;
  font: 600 16px "微软雅黑";
  vertical-align: middle;
}
.gouwuche{
  margin-right: 10px;
  display: inline-block;
  font-size: 25px;
}
.utilskipContainer p{
  margin-top: 10px;
}
.deleteAll{
  text-decoration: none;
  color: #777;
  display: inline-block;
  background: #164ad6;
  padding: 10px;
  border: 1px solid #1141c3;
  border-radius: 10px;
}
.deleteAll:hover{
  text-decoration: none;
  color: #dacfcf;
}
.utilskipContainer{
  text-align: center;
  background: #6d6b6b;
}
.NoDataFont{
  display: inline-block;
  font-size: 30px;
  color: #da2323;
}
.NoDataContainer{
  height: 200px;
  text-align: center;
  margin-bottom: 50px;
  line-height: 200px;
}
.NoDataSkip{
  color: #d67e10d4;
  font: 600 18px/30px "微软雅黑";
}
.continueShopping{
  text-align: center;
  display: inline-block;
  width: 120px;
  height: 30px;
  background: #ddd8e8;
  border: 1px solid #7b7a94b0;
  font: 600 14px/30px "微软雅黑";
  border-radius: 20px;
  cursor: pointer;
  color: #868484e0;
}
.continueShopping:hover{
  background:#eeecf3;
  color: #444;
}
a{
  text-decoration: none;
  color: #635151;
}
.Next{
  text-align: center;
  display: inline-block;
  width: 100px;
  height: 30px;
  background: #52d619c9;
  border: 1px solid #37a208c9;
  font: 600 18px/30px "微软雅黑";
  border-radius: 20px;
  cursor: pointer;
  color: #c1b3b3;
}
.Next:hover{
  background:#4ef506c9;
  color: #ddd;
}
.enabled{
  text-align: center;
  display: inline-block;
  width: 100px;
  height: 40px;
  font: 600 18px/40px "微软雅黑";
  border-radius: 20px;
  cursor: pointer;
  border: 1px solid  #e4d2d2b0;
  background:#dedcdcd4;
  color: #655c5c;
}
.selectAllTitle{
  color: #aaa;
  margin-right: 30px;
  display: inline-block;
}
.selectedAll{
  vertical-align: middle;
  display: inline-block;
  width: 19px;
  height: 19px;
  background: #ddd;
}
  .cartGoodList input{
    width: 15px;
    height: 15px;
    margin-right: 30px;
  }
.selectBtn{
  margin: 2px;
  display: inline-block;
  width: 15px;
  height: 15px;
  background: #e88c1e;
  border-radius: 7px;
}

.goodNumber{
  width: 40px;
  height: 30px;
  display: inline-block;
  border: 1px solid #ddd;
  vertical-align: middle;
  text-align: center;
  line-height: 30px;
}
.cartGoodList{
  text-align: center
}
.cartGoodList span{
  font: 600 14px/130px "微软雅黑";
  display: inline-block;
}
.cartTitle{
  height: 40px;
  text-align: center;
  background: #6d6b6b;
}
.cartTitle p{
  font: 500 16px/40px "微软雅黑";
  color: #fff;
}
.selected{
  display: inline-block;
}
.image{
  margin-right: 20px;
  width: 100px;
  height: 100px;
  display: inline-block
}

.cartGoodName{
  display: inline-block;
}
.operation{
  vertical-align: middle;
  display: inline-block;
  width: 30px;
  height: 30px;
  background: #ddd;
}
.cartOption{
  text-align: center;
  padding: 55px 0;
}
.increase{
  cursor: pointer;
  font: 600 16px/30px "微软雅黑";
  margin-left: -5px;
}
.decrease{
  cursor: pointer;
  font: 600 16px/30px "微软雅黑";
  margin-right: -5px;
}
.deleteFont{
  display: inline-block;
  color: #aaa;
  font-size: 25px;
  cursor: pointer;
}
.deleteFont:hover{
   color: #000;
}
</style>



