<template>
  <div>
    <Banner/>
    <div class="container">
      <div class="row">
        <div class="col-md-11 col-md-offset-1">
          <steps :current="2">
              <step title="提交订单" content="订单的提交"></step>
              <step title="设置地址" content="设置收货地址"></step>
              <step title="确认订单" content="完成订单的确定"></step>
              <step title="完成支付" content="完成订单的支付"></step>
          </steps>
        </div>
      </div>
      <div class="row">
        <div class="col-md-11">
          <h4 class="page-header"><i class="iconfont icon-goumaiicon goumaiicon"></i>ＰＵＲＣＨＡＳＥ</h4>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12 cartTitle">
          <div class="col-md-2 col-md-offset-1">
              <p>ＦＲＵＩＴ</p>
          </div>
          <div class="col-md-2 col-md-offset-1">
            <p>ＰＲＩＣＥ</p>
          </div>
          <div class="col-md-2 col-md-offset-1">
            <p>ＮＵＭＢＥＲ</p>
          </div>
          <div class="col-md-2 col-md-offset-1">
            <p>ＴＯＴＡＬＰＲＩＣＥ</p>
          </div>
        </div>
      </div>
      <div class="row gooodsList">
        <div class="col-md-12" v-for="item in cartList" :key='item.id'>
          <div class="col-md-3" >
            <p class="cartGoodList">
              <img :src="'../../static/mix-img/'+item.image[0].min" :alt="item.name" class="image">
              <span class="cartGoodName">{{item.name}}</span>
            </p>
          </div>
          <div class="col-md-3">
            <p class="cartGoodList">
              <span class="cartFruit">{{item.price | currency('￥')}}</span>
            </p>
          </div>
          <div class="col-md-3">
            <p class="cartOption">
              <span class="goodNumberSubmit">{{item.goodsNumber}}</span>
            </p>
          </div>
          <div class="col-md-3">
            <p class="cartGoodList">
              <span class="cartFruit">{{(item.goodsNumber*item.price) | currency('￥')}}</span>
            </p>
          </div>
        </div>
      </div>
      <div class="row" >
        <div class="col-md-12 address">
          <div class="addressContainer">
            <p><span>收货人：</span><span>{{addressList.recipientName}}</span></p>
            <p><span>联系人电话：</span><span>{{addressList.recipientTel}}</span></p>
            <p><span>收货人邮编：</span><span>{{addressList.recipientPostcodes}}</span></p>
            <p><span>收货地址:</span><span>{{addressList.recipientDetailAddress}}</span></p>
          </div>
        </div>
      </div>
      <div class="row ">
        <div class="col-md-4 pull-right address">
          <div class="addressContainer">
            <p><span>税前费用：</span><span>{{total | currency('￥')}}</span></p>
            <p><span>税费：</span><span>{{taxes | currency('￥')}}</span></p>
            <p><span>运费：</span><span>{{freight | currency('￥')}}</span></p>
            <p><span>进口关税：</span><span>{{tariffs | currency('￥')}}</span></p>
            <p><span>税后金额：</span><span>{{TotalMoney | currency('￥')}}</span></p>
          </div>
        </div>
      </div>
      <div class="row GoOrBack">
        <div class="col-md-3 pull-right">
          <span　class="back" @click="back">ＢＡＣＫ</span>
          <span class="submit" @click="payFor">ＰＡＹＦＯＲ</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Banner from './Banner'
import {steps,step} from 'iview'
import axios from 'axios'
import {currency} from '../../util/currency'
export default{
  data(){
    return {
      cartList:[],
      addressList:{},
      taxes:2.00,
      freight:10.00,
      tariffs:0.00,
      orderId:''
    }
  },
  computed:{

    total(){

      let temp=0;

      this.cartList.forEach(item=>{

        temp += item.goodsNumber*item.price

      })
      return temp;
    },
    TotalMoney(){

      let total =  0;

      this.cartList.forEach(item=>{

        total += item.goodsNumber*item.price

      })

      total +=this.taxes+this.freight+this.tariffs;

      return total;
    }
  },
  filters:{
    currency
  },
  components:{
    Banner,
    steps,
    step
  },
  methods:{

    back(){
      this.$router.push({path:'/setAddr'})
    },

    payFor(){

      let money = this.TotalMoney;

      axios.post('/submitOrder',{

        params:{

          totalMoney:money

        }

      }).then(res=>{

        if(res.data.status === 0){

          this.orderId = res.data.result;

         return this.deleteAlreadyBuyGoods();

        }else{

          alert('提交失败');

        }

      }).then(res1=>{

        if(res1.data.status === 0){

          let orderId = this.orderId;

          this.$router.push({path:'/success',query:{orderId}});

        }else{

          alert('删除已购买物品失败');

        }

      }).catch(err=>{

        alert('其他错误');

      })
    },

    deleteAlreadyBuyGoods(){

      return axios.post('/delAlreadyGoods')

    },

    getSelectGoods(){

      axios.post('/getSelectGoods').then(res=>{

      if(res.data.status === 0){

         this.cartList = res.data.result;

        return this.getSelectAddress();

      }else{

        alert('请求选中商品失败');

      }

      }).then(res1=>{

        if(res1.data.status === 0){

          this.addressList = res1.data.result;

        }else{

          alert('请求选中地址失败');

        }

      }).catch(err=>{

        console.log(err)

      })
    },
    getSelectAddress(){

      return axios.post('/getSelectAddress');
    }

  },
  mounted(){

    this.getSelectGoods();
  }
}
</script>
<style>
.GoOrBack{
  margin: 25px 0px;
}
.back{
  display: inline-block;
  padding: 10px 30px;
  background: #ea535345;
  cursor: pointer;
  color: #807e7ebf;
}
.back:hover{
  background: #ea535369;
  color:#423e3ee3;
}
.submit{
  display: inline-block;
  padding: 10px 20px;
  background: #0fc0da4d;
  cursor: pointer;
  color: #807e7ebf;z-index:
}
.submit:hover{
  background: #0fc0da69;
  color:#423e3ee3;
}
.goumaiicon{
  display: inline-block;
  font-size: 28px;
  margin-right: 10px;
  color: #f33434e0;
}

.cartGoodList{
  text-align: center
}
.cartGoodList span{
  font: 600 16px/130px "微软雅黑";
  display: inline-block;
}
.image{
  margin-right: 20px;
  width: 100px;
  height: 100px;
  display: inline-block
}

.cartGoodName{
  display: inline-block;
}
.cartOption{
  text-align: center;
  padding: 55px 0;
}
.goodNumberSubmit{
  width: 40px;
  height: 30px;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  line-height: 30px;
}
.cartGoodList{
  text-align: center
}
.cartGoodList span{
  font: 500 16px/130px "微软雅黑";
  display: inline-block;
}
.gooodsList{
  text-align: center;
  margin-top: 15px;
  background: #dddddd42;
  color: #7b7979cc;
  font: 500 16px "微软雅黑";
}
.address{
  margin-top: 15px;
  background: #dddddd69;
}
.addressContainer p{
   font: 500 14px "微软雅黑";
  color: #7b7979cc;
  line-height: 30px;
  margin-left: 40px;
}
</style>



